import React from 'react';
import './loginForm.css';

import {useNavigate } from 'react-router-dom';

class loginForm extends React.Component {

    constructor() {
        super();
        this.state = {
            email: "",
            password: "",
            pending: false
        };
        
    }

    handleEmailChange = (event) => {
        this.setState({email: event.target.value});
    }

    handlePasswordChange = (event) => {
        this.setState({password: event.target.value});
    }

    getFormData = () => {
        let email = this.state.email;
        let password = this.state.password;
        return {email: email, password: password};
    }

    onFormSubmit = (event) => {
        event.preventDefault();
        if(!this.state.pending) {
            this.setState({pending : true});
            let data = this.getFormData();

            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(data)
            };

            fetch('/php/login.php', requestOptions)
                .then(response => response.json())
                .then(data => {
                    if(data.response) {
                        const navigate = useNavigate();
                        navigate('/home');
                    } else {
                        this.setState({pending : false}); 
                    }
                })

        }
    }

    render() {
        return(
        <div id="login-div">
            <form onSubmit={this.onFormSubmit} id="login-form">
                <label htmlFor="email">E-Mail</label>
                <input type="email" autoComplete="username" id="email" value={this.state.email} onChange={this.handleEmailChange} required/>
                <label htmlFor="password">Heslo</label>
                <input type="password" autoComplete="current-password" minLength="6" id="password" value={this.state.password} onChange={this.handlePasswordChange} required/>
                <button type="submit">{this.state.pending ? "Přihlašování" : "Přihlásit"}</button>
            </form>
            <div id="forgotten-password">
                <p>Zapomenuté heslo? <a href="#">Obnovení hesla</a></p>
            </div>
        </div>
        )
    }
}

export default loginForm;